body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.input {
  width: 80%;
  height: 40px;
  font-size: 20px;
  padding: 0 5px;
  padding-right: 50px;
}

.search-button {
  height: 44px;
  font-size: 20px;
  background-color: green;
  color: white;
  border: none;
  width: 150px;
  margin-top: 10px;
  border-radius: 10px;
}

span {
  position: relative;
  right: 30px;
  margin: 0;
  padding: 0;
  font-size: 25px;
  top: 2px;
  color: blue;
  font-weight: 500;
  cursor: pointer;
}

@media only screen and (min-width: 720px) {
  .input {
    width: 420px;
    margin: 10px;
  }
  span {
    right: 45px;
  }
}

