.card-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
}

@media only screen and (min-width: 720px) {
  .card-list {
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
}

.error {
  border: 1px solid red;
  width: 300px;
  font-size: 20px;
  margin: 0 auto;
  margin-top: 10px;
  position: relative;
  right: 100%;
  visibility: hidden;
  /* display: none; */
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

@-webkit-keyframes fade {
  0% {
    visibility: visible;
    right: 100%;
  }
  25% {
    right: 0%;
  }
  75% {
    right: 0%;
  }
  100% {
    right: 100%;
    display: none;
  }
}

@keyframes fade {
  0% {
    visibility: visible;
    right: 100%;
  }
  25% {
    right: 0%;
  }
  75% {
    right: 0%;
  }
  100% {
    right: 100%;
    display: none;
  }
}

